<template>
    <div class="lineCharts">
        <div ref="lineCharts" class="bar_bda"></div>
    </div>
</template>

<script>
  import Interface from '@/base/mixin/channel-preference';
  let option = {
    title: {
      show:false,
      text: '折线图堆叠'
    },
    tooltip: {
      trigger: 'axis'
    },
    legend: {
      padding:[30,0,0,0],
      data: [],
      show:false
    },
    grid: {
      left: '3%',
      right: '4%',
      bottom: '3%',
      containLabel: true
    },
    toolbox: {
      feature: {
        saveAsImage: {}
      }
    },
    xAxis: {
      type: 'category',
      boundaryGap: false,
      data: []
    },
    yAxis: {
      type: 'value',
      axisTick: {
        show: false  //坐标轴刻度线
      },
      axisLine: { //轴线
        show: false
      },
    },
    series: []
  }
  export default {
    name: "lineCharts",
    mixins: [Interface],
    components:{

    },
    props:{

    },
    data(){
      return {
        lineOption:option,
      }
    },
    mounted() {
      // this.init();
    },
    methods:{
      init(legendData=[],series=[],dataX=[]){
        this.lineOption.legend.data = legendData;
        this.lineOption.xAxis.data = dataX;
        this.lineOption.series = series;
        let echarts = require('echarts');
        this.mychart = echarts.init(this.$refs.lineCharts)
        this.mychart.setOption(this.lineOption);
        let mychart = echarts.init(this.$refs.lineCharts, true)  //监听窗口变化重新加载
        window.addEventListener("resize", mychart.resize); //监听窗口变化重新加载
      },
    }
  }
</script>

<style scoped lang="scss">
    .lineCharts{
        height: 100%;
        .bar_bda{
            height: 100%;
            min-height: 100px;
        }

    }
</style>